<template>
    <div class="addBankCards" id="addBankCards">
        <Head :title="title"></Head>
        <div class="addBankCardsView">
            <div class="cardView">
                <div class="card">
                    <div class="item display_csb">
                        <div class="name"></div>
                        <div class="rightName">
                            <input type="text" v-model="cardholder" placeholder="" class="inputName">
                        </div>
                    </div>
                    <div class="item display_csb">
                        <div class="name"></div>
                        <div class="rightName">
                            <input type="text" v-model="idcard" placeholder="" class="inputName">
                        </div>
                    </div>
                    <div class="item display_csb">
                        <div class="name"></div>
                        <div class="rightName">
                            <input type="text" v-model="bank_card_number" placeholder="" class="inputName">
                        </div>
                    </div>
                    <div class="item display_csb">
                        <div class="name"></div>
                        <div class="rightName display_cc">
                            <div class="select"></div>
                            <img src="../../common/images/index/back.png" alt="" class="rightselect">
                        </div>
                    </div>
                    <div class="item display_csb">
                        <div class="name"></div>
                        <div class="rightName">
                            <input type="text" v-model="reserved_mobile" placeholder="" class="inputName">
                        </div>
                    </div>
                    <!-- <div class="item display_csb">
                        <div class="name"></div>
                        <div class="rightName testgetCode">
                            <input type="text" v-model="bank_code" placeholder="" class="inputName">
                            <div class="line"></div>
                            <div class="getCode" @click="getCode">{{codeMsg}}</div>
                        </div>
                    </div> -->
                </div>
            </div>
            <div class="btn display_cc">
                <div class="btn_btn display_cc" @click="submitBank"></div>
            </div>
        </div>
    </div>
</template>

<script>
import {apiAddress} from "@/utils/apiAddress"
import commmon from "@/common/js/common"
    export default {
        data() {
            return {
                title: "",
                sending: true, //
                disabled: false, //
                second: 60, //
                result: null,
                bank_code:"",//
                reserved_mobile:"",//
                bank_card_number:"",//
                idcard:"",//
                cardholder:"",//
                codeMsg:"",
            }
        },
        created(){
            clearInterval(this.result);
        },
        methods:{
            submitBank(){
                //
                let data={
                    cardholder:this.cardholder,	
                    idcard:this.idcard,
                    bank_card_number:this.bank_card_number,		
                    bank_name:"",
                    reserved_mobile	:this.reserved_mobile	,	
                    bank_code:this.bank_code,
                }
                if(!this.cardholder){
                    this.$toast({
                        message:"",
                        position:"bottom"
                    })
                    return
                }
                if(!commmon.isCardNo(this.idcard)){
                    this.$toast({
                        message:"",
                        position:"bottom"
                    })
                    return
                }
                // if(!commmon.bankCardValidate(this.bank_card_number)){
                //     this.$toast({
                //         message:"",
                //         position:"bottom"
                //     })
                //     return
                // }
                if(!commmon.checkMobile(this.reserved_mobile)){
                    this.$toast({
                        message:"",
                        position:"bottom"
                    })
                    return
                }
                // if(!this.bank_code){
                //     this.$toast({
                //         message:"",
                //         position:"bottom"
                //     })
                //     return
                // }
                apiAddress.bindBankcard(data).then((result)=>{
                    if(result.code==1){
                        this.$toast({
                            message:"",
                            position:"bottom"
                        })
                        this.$router.go(-1)
                    }else{
                         this.$toast({
                            message:result.msg,
                            position:"bottom"
                        })
                    }
                })
            },
            //
            getCode(){
                let phone=this.reserved_mobile;
                let isMobile=commmon.checkMobile(phone);
                if(!isMobile){
                    this.$toast({
                        message: "",
                        position:"bottom"
                    })
                    return
                }
                let data={
                    mobile:phone,
                    event:"register"
                }
                this.sending = false;
                this.disabled = true;
                this.timeDown();
                apiAddress.sendsms(data).then((result)=>{ 
                    if(result.code==1){
                        this.$toast({
                            message:"",
                            position:"bottom"
                        })
                    }else{
                       this.$toast({
                            message:result.msg,
                            position:"bottom"
                        }) 
                    }
                })
            },
            timeDown() {
                this.result = setInterval(() => {
                    --this.second;
                    this.codeMsg = this.second + "s";
                    if (this.second < 0) {
                    clearInterval(this.result);
                    this.sending = true;
                    this.disabled = false;
                    this.second = 60;
                    this.codeMsg = "";
                    }
                }, 1000);
            },
            resizepage() {
                document.getElementsByTagName("html")[0].style.height =
                    window.innerHeight + "px";
                
                if (document.getElementById("addBankCards")) {
                    document.getElementById("addBankCards").style.height =
                    window.innerHeight+"px";
                }
            },
            init(){
                var self = this; //thiswindowsthis
                window.addEventListener("load", function() {
                    //
                    self.resizepage();
                });
                window.addEventListener("resize", function() {
                    //
                    self.resizepage();
                });
                setTimeout(() => {
                    window.addEventListener("load", function() {
                    //
                    self.resizepage();
                    });
                    window.addEventListener("resize", function() {
                    //
                    self.resizepage();
                    });
                }, 200);
            },
        },
        mounted() {
            this.init();
            document.getElementsByTagName("html")[0].style.height =
            window.innerHeight + "px";
            if (document.getElementById("addBankCards")) {
                document.getElementById("addBankCards").style.height =window.innerHeight+"px";
            }
        }
    }
</script>

<style lang="scss" scoped>
@import "../../common/css/mixin.scss";
.addBankCards{
    background-color: #f5f5f5;
    .addBankCardsView{
        padding-top: pxTorem(45px);
        .cardView{
            margin-top: pxTorem(21px);
            background-color: #fff;
            .card{
                margin: 0 pxTorem(39px);
                .item{
                    padding: pxTorem(15px) 0;
                    border-bottom: solid 1px #e6e6e6;
                    .name{
                        font-size: pxTorem(16px);
                        color: #666666;
                        font-weight: 500
                    }
                    .rightName{
                        .inputName{
                            outline: 0;
                            border: 0;
                            font-size: pxTorem(16px);
                            color: #999999;
                        }
                        .select{
                            font-size: pxTorem(16px);
                            color: #999999;
                            padding-right: pxTorem(5px);
                        }
                        .rightselect{
                            width: pxTorem(10px);
                            height: pxTorem(20px);
                        }
                        
                    }
                    .testgetCode{
                        display: flex;
                        display: -webkit-flex;
                        .inputName{
                            width: pxTorem(50px);
                        }
                        .line{
                            border-left: solid 1px #cccccc;
                            margin: 0 pxTorem(5px);
                        }
                        .getCode{
                            font-size: pxTorem(16px);
                            color: #333333;
                        }
                    }
                }
            }
        }

        .btn{
            .btn_btn{
                font-size: pxTorem(16px);
                color: #fff;
                width: pxTorem(295px);
                background-color: #2ca7fa;
                border-radius: pxTorem(5px);
                padding: pxTorem(10px) 0;
                margin-top: pxTorem(40px)
            }
        }
    }
}
</style>


