import axios from "axios";
import Qs from 'qs'
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded'
const http = {
    /**
     * getget
     * @param {String} url [url]
     * @param {Object} params []
     */
    get: function(url, params) {
        return new Promise((resolve, reject) => {
            axios
                .get(url, {
                    params: params
                })
                .then(res => {
                    resolve(res.data);
                })
                .catch(err => {
                    reject(err.data);
                });
        });
    },
    /**
     * deletedelete
     * @param {String} url [url]
     * @param {Object} params []
     */
    delete: function(url, params) {
        return new Promise((resolve, reject) => {
            axios
                .delete(url, {
                    params: params
                })
                .then(res => {
                    resolve(res.data);
                })
                .catch(err => {
                    reject(err.data);
                });
        });
    },
    /**
     * postpost
     * @param {String} url [url]
     * @param {Object} params []
     */
    post: function(url, params) {
        return new Promise((resolve, reject) => {
            axios
                .post(url + "?" + Qs.stringify(params))
                .then(res => {
                    resolve(res.data);
                })
                .catch(err => {
                    reject(err.data);
                });
        });
    },
    postJson: function(url, params) {
        return new Promise((resolve, reject) => {
            axios
                .post(url, params)
                .then(res => {
                    resolve(res.data);
                })
                .catch(err => {
                    reject(err.data);
                });
        });
    },
    postNone: function(url, params) {
        return new Promise((resolve, reject) => {
            axios
                .post(url, params)
                .then(res => {
                    resolve(res.data);
                })
                .catch(err => {
                    reject(err.data);
                });
        });
    },
    /**
     * putput
     * @param {String} url [url]
     * @param {Object} params []
     */
    put: function(url, params) {
        return new Promise((resolve, reject) => {
            axios
                .put(url, params)
                .then(res => {
                    resolve(res.data);
                })
                .catch(err => {
                    reject(err.data);
                });
        });
    }
};
export { http };


