export default {
    pxToRem(px) {
        let html = document.querySelector('html');
        const fontSize = window.getComputedStyle(html).fontSize;
        return parseInt(px) / parseFloat(fontSize) + 'rem';
    },
    // Returns a function, that, when invoked, will only be triggered at most once
    // during a given window of time. Normally, the throttled function will run
    // as much as it can, without ever going more than once per `wait` duration;
    // but if you'd like to disable the execution on the leading edge, pass
    // `{leading: false}`. To disable execution on the trailing edge, ditto.
    // 
    //  wait(Number) milliseconds  func 
    //  options  {leading: false}
    //  wait milliseconds  func
    //  options  {trailing: false}
    // 
    // **Notice: options  leading  trailing  false**
    // 
    // var throttled = _.throttle(updatePosition, 100);
    // $(window).scroll(throttled);
    //  A  B console.log 
    // _.throttle(function, wait, [options])
    // sample 1: _.throttle(function(){}, 1000)
    // print: A, B, B, B ...
    // sample 2: _.throttle(function(){}, 1000, {leading: false})
    // print: B, B, B, B ...
    // sample 3: _.throttle(function(){}, 1000, {trailing: false})
    // print: A, A, A, A ...
    // ----------------------------------------- //
    throttle(func, wait, options) {
        var context, args, result;
        // setTimeout  handler
        var timeout = null;
        // 
        // 
        var previous = 0;
        //  options 
        //  options 
        if (!options)
            options = {};
        var later = function() {
            //  options.leading === false
            //  previous  0
            // 
            previous = options.leading === false ? 0 : new Date().getTime();
            timeout = null;
            // console.log('B')
            result = func.apply(context, args);
            //  timeout  null 
            // 
            if (!timeout)
                context = args = null;
        };
        // scroll
        // 
        // _.throttle 
        return function() {
            // 
            var now = new Date().getTime();
            //  previous  0 previous 
            // options.leading === false
            //  previous  now 
            // 
            if (!previous && options.leading === false)
                previous = now;
            //  func 
            var remaining = wait - (now - previous);
            context = this;
            args = arguments;
            // remaining <= 0
            //  {leading: false}
            //  previous  0wait - (now - previous)  <= 0
            //  previous  now
            // ========= //
            // remaining > wait
            //  func 
            // @see https://blog.coding.net/blog/the-difference-between-throttle-and-debounce-in-underscorejs
            // ========= //
            // console.log(remaining) 
            if (remaining <= 0 || remaining > wait) {
                if (timeout) {
                    clearTimeout(timeout);
                    // 
                    timeout = null;
                }
                // 
                previous = now;
                // 
                // result 
                // console.log('A')
                result = func.apply(context, args);
                // 
                //  timeout  null  if 
                if (!timeout)
                    context = args = null;
            } else if (!timeout && options.trailing !== false) { // 
                //  if 
                //  {trailing: false}
                //  remaining milliseconds  later 
                timeout = setTimeout(later, remaining);
            }
            // 
            return result;
        };
    },
    //
    debounce(method, context) {
        let timer = null;
        return function() {
            clearTimeout(timer);
            timer = setTimeout(function() {
                method.call(context);
            }, 100);
        }
    },

    //cookie
    getCookie(c_name) {
        if (document.cookie.length > 0) {
            let c_start = document.cookie.indexOf(c_name + "=")
            if (c_start != -1) {
                c_start = c_start + c_name.length + 1
                let c_end = document.cookie.indexOf(";", c_start)
                if (c_end == -1) c_end = document.cookie.length
                return decodeURI(document.cookie.substring(c_start, c_end))
            }
        }
        return "";
    },
    setCookies(name, value, time) {
        var cookieString = name + "=" + encodeURI(value) + ";";
        if (time != 0) {
            var Times = new Date();
            Times.setTime(Times.getTime() + time);
            cookieString += "expires=" + Times.toGMTString() + ";"
        }
        document.cookie = cookieString;
    },
    /**
     * 
     */
    checkMobile(phone) {
        let reg = /(^1[3|4|5|7|8][0-9]{9}$)/;
        return reg.test(phone)
    },
    /**
     * ,48
     */
    checkUsername(name) {
        let reg = /^(?!_)(?!.*?_$)[a-zA-Z0-9_\u4e00-\u9fa5]{4,8}$/;
        return reg.test(name)
    },
    /**
     * ,6202
     */
    checkPassword(psw) {
        let reg = /(?!^[0-9]+$)(?!^[A-z]+$)(?!^[^A-z0-9]+$)^.{6,20}$/;
        return reg.test(psw)
    },
    /**
     * 
     */
    upload(e) {
        let file = e.target.files[0];
        if (file) {
            let formData = new FormData(); //form
            formData.append('file0', file); //appendform
            let config = {
                headers: { 'Content-Type': 'multipart/form-data' },
                transformRequest: [function(data) {
                    return data
                }]
            }; //
            return $http.post('/Pic/uploadPic', formData, config)
        }
    },
    /**
     * 
     */
    countDown(time, diff) {
        //time php
        let t = Number(time * 1000) - Number(diff);
        if (t < 0) {
            return false;
        }
        let d = Math.floor(t / 1000 / 60 / 60 / 24);
        let h = Math.floor(t / 1000 / 60 / 60 % 24);
        let m = Math.floor(t / 1000 / 60 % 60);
        let s = Math.floor(t / 1000 % 60);
        if (d < 10) {
            d = '0' + d;
        }
        if (h < 10) {
            h = '0' + h;
        }
        if (m < 10) {
            m = '0' + m;
        }
        if (s < 10) {
            s = '0' + s;
        }
        return d + ':' + h + ':' + m + ':' + s + ''
    },
    isLogin() {
        return api.checkLogin().then(res => {
            if (res.data.code === 201) {
                location.href = '/#/login';
                return false;
            }
            return true;
        })

    },
    //
    toDecimal2(x) {
        let n = parseFloat(x);
        if (isNaN(n)) {
            return false;
        }
        let f = Math.round(x * 100) / 100;
        let s = f.toString();
        let rs = s.indexOf('.');
        if (rs < 0) {
            rs = s.length;
            s += '.';
        }
        while (s.length <= rs + 2) {
            s += '0';
        }
        return s;
    },
    //
    tips(data, msg) {
        data.status = true;
        data.message = msg;
        return setTimeout(() => {
            data.status = false
        }, 1500)
    },
    //
    isWX() {
        let ua = navigator.userAgent.toLowerCase();
        if (ua.match(/MicroMessenger/i) == "micromessenger") {
            return true;
        } else {
            return false;
        }
    },
    //
    isCardNo(card) {
        // 1518151817X  
        var reg = /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/;
        if (reg.test(card) === false) {
            return false;
        } else {
            return true
        }
    },
    //
    bankCardValidate(bankno) {
        const lastNum = +bankno.substr(bankno.length - 1, 1);
        const first15Num = bankno.substr(0, bankno.length - 1);
        const newArr = [];
        for (let i = first15Num.length - 1; i > -1; i -= 1) {
            newArr.push(first15Num.substr(i, 1));
        }
        const arrJiShu = [];
        const arrJiShu2 = [];
        const arrOuShu = [];
        for (let j = 0; j < newArr.length; j += 1) {
            if ((j + 1) % 2 === 1) {
                if (parseInt(newArr[j], 10) * 2 < 9) {
                    arrJiShu.push(parseInt(newArr[j], 10) * 2);
                } else {
                    arrJiShu2.push(parseInt(newArr[j], 10) * 2);
                }
            } else {
                arrOuShu.push(newArr[j]);
            }
        }
        let sumJiShu = 0;
        let sumOuShu = 0;
        let sumJiShuChild1 = 0;
        let sumJiShuChild2 = 0;
        let sumTotal = 0;
        for (let m = 0; m < arrJiShu.length; m += 1) {
            sumJiShu += parseInt(arrJiShu[m], 10);
        }

        for (let n = 0; n < arrOuShu.length; n += 1) {
            sumOuShu += parseInt(arrOuShu[n], 10);
        }

        for (let p = 0; p < jishuChild1.length; p += 1) {
            sumJiShuChild1 += parseInt(jishuChild1[p], 10);
            sumJiShuChild2 += parseInt(jishuChild2[p], 10);
        }
        sumTotal = parseInt(sumJiShu, 10) + parseInt(sumOuShu, 10) +
            parseInt(sumJiShuChild1, 10) + parseInt(sumJiShuChild2, 10);
        const k = parseInt(sumTotal, 10) % 10 === 0 ? 10 : parseInt(sumTotal, 10) % 10;
        const luhn = 10 - k;
        if (lastNum === luhn) {
            return true;
        } else {
            return false;
        }
    }
}


